"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Separator } from "@/components/ui/separator"
import { Loader2, RefreshCw, Trash } from "lucide-react"
import { BackButton } from "@/components/ui/BackButton"
import { useToast } from "@/components/ui/use-toast"
import apiClient from "@/services/apiClient"

export default function DiagnosticoUsuarioPage() {
  const { toast } = useToast()
  const [userId, setUserId] = useState("")
  const [loading, setLoading] = useState(false)
  const [deleting, setDeleting] = useState(false)
  const [result, setResult] = useState<any>(null)
  const [error, setError] = useState<string | null>(null)

  const buscarUsuario = async () => {
    if (!userId || isNaN(Number(userId))) {
      toast({
        title: "ID inválido",
        description: "Por favor, informe um ID de usuário válido.",
        variant: "destructive",
      })
      return
    }

    setLoading(true)
    setError(null)
    setResult(null)

    try {
      const response = await apiClient.get(`/usuarios/${userId}`)
      setResult({
        status: response.status,
        data: response.data,
      })
      toast({
        title: "Usuário encontrado",
        description: `Usuário ${response.data.nome} encontrado com sucesso.`,
      })
    } catch (err: any) {
      console.error("Erro ao buscar usuário:", err)
      setError(
        `Erro ao buscar usuário: ${
          err.response
            ? `Status ${err.response.status} - ${JSON.stringify(err.response.data)}`
            : err.message || "Erro desconhecido"
        }`,
      )
      toast({
        title: "Erro",
        description: "Não foi possível buscar o usuário. Verifique o console para mais detalhes.",
        variant: "destructive",
      })
    } finally {
      setLoading(false)
    }
  }

  const excluirUsuario = async () => {
    if (!userId || isNaN(Number(userId))) {
      toast({
        title: "ID inválido",
        description: "Por favor, informe um ID de usuário válido.",
        variant: "destructive",
      })
      return
    }

    setDeleting(true)
    setError(null)

    try {
      // Obter o token do localStorage
      const token = localStorage.getItem("token")
      if (!token) {
        throw new Error("Token não encontrado")
      }

      // Fazer a requisição DELETE diretamente com axios
      const response = await apiClient.delete(`/usuarios/${userId}`, {
        headers: {
          Authorization: `Bearer ${token}`,
        },
      })

      setResult({
        status: response.status,
        data: response.data || { message: "Usuário excluído com sucesso" },
      })

      toast({
        title: "Usuário excluído",
        description: `Usuário com ID ${userId} foi excluído com sucesso.`,
      })
    } catch (err: any) {
      console.error("Erro ao excluir usuário:", err)
      setError(
        `Erro ao excluir usuário: ${
          err.response
            ? `Status ${err.response.status} - ${JSON.stringify(err.response.data)}`
            : err.message || "Erro desconhecido"
        }`,
      )
      toast({
        title: "Erro",
        description: "Não foi possível excluir o usuário. Verifique o console para mais detalhes.",
        variant: "destructive",
      })
    } finally {
      setDeleting(false)
    }
  }

  return (
    <div className="container py-6">
      <div className="flex items-center justify-between mb-6">
        <div className="flex items-center gap-4">
          <BackButton />
          <h1 className="text-3xl font-bold">Diagnóstico de Usuário</h1>
        </div>
      </div>

      <Card className="mb-6">
        <CardHeader>
          <CardTitle>Buscar ou Excluir Usuário</CardTitle>
          <CardDescription>
            Informe o ID do usuário para buscar informações ou excluir diretamente do banco de dados.
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid gap-4">
            <div className="grid gap-2">
              <Label htmlFor="userId">ID do Usuário</Label>
              <div className="flex gap-2">
                <Input
                  id="userId"
                  placeholder="Digite o ID do usuário"
                  value={userId}
                  onChange={(e) => setUserId(e.target.value)}
                />
                <Button onClick={buscarUsuario} disabled={loading}>
                  {loading ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <RefreshCw className="mr-2 h-4 w-4" />}
                  Buscar
                </Button>
                <Button variant="destructive" onClick={excluirUsuario} disabled={deleting}>
                  {deleting ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Trash className="mr-2 h-4 w-4" />}
                  Excluir
                </Button>
              </div>
            </div>
          </div>
        </CardContent>
      </Card>

      {error && (
        <Card className="mb-6 border-destructive">
          <CardHeader>
            <CardTitle className="text-destructive">Erro</CardTitle>
          </CardHeader>
          <CardContent>
            <pre className="bg-destructive/10 p-4 rounded-md overflow-auto text-destructive whitespace-pre-wrap">
              {error}
            </pre>
          </CardContent>
        </Card>
      )}

      {result && (
        <Card>
          <CardHeader>
            <CardTitle>Resultado</CardTitle>
            <CardDescription>Status: {result.status}</CardDescription>
          </CardHeader>
          <CardContent>
            <Separator className="my-4" />
            <div className="mt-4">
              <h3 className="text-lg font-semibold mb-2">Dados:</h3>
              <pre className="bg-muted p-4 rounded-md overflow-auto whitespace-pre-wrap">
                {JSON.stringify(result.data, null, 2)}
              </pre>
            </div>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
